<template>
  <div class="course" v-loading.fullscreen.lock="loading">
    <div class="course_body" v-if="currentStageData">
        <About
            v-if="currentStageData.type === 'about'"
            :description="course['detail']"
            @next="goToModules"
        />
        <Module
            v-else-if="currentStageData.type === 'module'" 
            :courseData="currentModuleData['themes']"
            :testing="currentModuleData['quiz']"
            :slug="course.slug"
            :disabledNextBtn="disabledNextBtn"
            @next="nextModule"
            @prev="prevModule"
        />
        <Final
            v-else-if="currentStageData.type === 'final'"
            :quiz="currentStageData?.quiz"
        />
    </div>
    <SideMenu
      :data="getMenuData"
      :currentStage="sidemenu"
      @currentMenu="currentMenu"
    />
  </div>
</template>

<script>
import About from '../../../../components/course/About.vue'
import Final from '../../../../components/course/Final.vue'
import Module from '../../../../components/course/Module.vue'
import SideMenu from '../../../../components/course/SideMenu.vue'
export default {
    components: { About, Module, Final, SideMenu },
    name: 'Course',
    data() {
        return {
            loading: false,
            disabledNextBtn: false,
            currentStageData: null,
            currentModuleData: null,
            sidemenu: {
                index: 0,
                type: 'stage',
                chapterIndex: null
            },
            course: {},
            stages: [
                {
                    type: 'about',
                    name: 'О курсе',
                    text: 'lorem'
                },
            ],
            // moduleTesting: {
            //     id: 20,
            //     available: true,
            //     duration: 60,
            //     score: 0,
            //     passedDate: '22.04.22',
            //     minScore: 60
            // }
        }
    },
    async mounted() {
        const page = this.$route.query.page
        const type = this.$route.query.type

        if (page) {
            this.sidemenu.index = Number(page) || 0
        } 
        if (type) {
            this.sidemenu.type = type
        }
        
        await this.getCourseDetails()
        this.currentMenu(this.sidemenu)
    },
    methods: {
        async getCourseDetails() {
            this.loading = true;
            let apiRoot = API_ROOT
            let slug = this.$route.params.slug
            await this.$http.get(`${apiRoot}/api/course/${slug}`)
                .then(res => {
                    this.course = res?.body
                    this.stages.push({
                      type: 'module',
                      modules: res?.body?.modules
                    })
                    if (res?.body?.quiz) {
                      this.stages.push({
                        name: 'Итоговое тестирование',
                        type: 'final',
                        quiz: res?.body?.quiz
                      })
                    }
                })
                .catch(err => {
                    if (err.status === 404) {
                        this.$router.push({ name: 'not-found' })
                    }
                    console.log(err)
                })
                .finally(() => {
                  this.loading = false;
                })
        },
        goToModules() {
          this.sidemenu.index = 0
          this.sidemenu.type = 'module'
          this.chapterIndex = null;

          const modulesIndex = this.stages.findIndex((stage) => stage.type === 'module')
          this.currentStageData = this.stages[modulesIndex]
          this.currentModuleData = this.stages[modulesIndex].modules[0]
          this.changeRouteQuery(0, 'module', null)
        },
        goToStage() {
          this.sidemenu.index = 0
          this.sidemenu.type = 'stage'
          this.chapterIndex = null;

          this.currentStageData = this.stages[0]
          this.currentModuleData = null
          this.changeRouteQuery(0, 'stage', null)
        },
        nextModule() {
          this.disabledNextBtn = false;
          const modulesIndex = this.stages.findIndex((stage) => stage.type === 'module')
          if (this.stages[modulesIndex].modules.length - 1 === this.sidemenu.index + 1)
            this.disabledNextBtn = true;

          if (this.stages[modulesIndex].modules.length <= this.sidemenu.index + 1)
            return;

          this.sidemenu.index++
          this.sidemenu.type = 'module'
          this.chapterIndex = null;

          this.currentStageData = this.stages[modulesIndex]
          this.currentModuleData = this.stages[modulesIndex].modules[this.sidemenu.index]
          this.changeRouteQuery(this.sidemenu.index, 'module', null)
        },
        prevModule() {
          const modulesIndex = this.stages.findIndex((stage) => stage.type === 'module')
          if (this.stages[modulesIndex].modules.length >= this.sidemenu.index + 1)
            this.disabledNextBtn = false;

          if (!this.sidemenu.index) {
            this.goToStage();
            return;
          }

          this.sidemenu.index--
          this.sidemenu.type = 'module'
          this.chapterIndex = null;

          this.currentStageData = this.stages[modulesIndex]
          this.currentModuleData = this.stages[modulesIndex].modules[this.sidemenu.index]

          this.changeRouteQuery(this.sidemenu.index, 'module', null)
        },
        changeRouteQuery(index, type, chapterIndex = null) {
          let query = {
            page: index,
            type: type,
          }
          if (chapterIndex) {
            query.chapter = chapterIndex
          }
          this.$router.replace({ query: query}).catch(()=>{})
        },
        currentMenu(data) {
            this.sidemenu.index = data.index
            this.sidemenu.type = data.type
            this.chapterIndex = data.chapterIndex
            if (data.type === 'stage') {
                this.currentStageData = this.stages[data.index]

            } else if (data.type === 'module') {
                const modulesIndex = this.stages.findIndex((stage) => stage.type === 'module')
                this.currentStageData = this.stages[modulesIndex]
                this.currentModuleData = this.stages[modulesIndex].modules[data.index]
            }
        },
    },
    computed: {
        getMenuData() {
            return {
                title: this.course.title,
                type: 'course',
                menu: this.stages,
                topics: null,
            }
        }
    }
}
</script>

<style lang="scss" scoped>

.course {
    display: flex;
    align-items: flex-start;
    gap: 32px;

    &_body {
        width: calc(100% - 432px);
    }
}
</style>
